import { NextApiRequest, NextApiResponse } from 'next';

// 模拟剧集数据
const episodes: Record<string, { id: string; title: string; isLocked: boolean }[]> = {
  '12': [
    { id: '1', title: '第1集', isLocked: false },
    { id: '2', title: '第2集', isLocked: false },
    { id: '3', title: '第3集', isLocked: true }, // 锁定
    { id: '4', title: '第4集', isLocked: false },
    // 其他集数据
  ],
};

export default function handler(req: NextApiRequest, res: NextApiResponse) {
  const { id } = req.query; // 获取视频 ID
  const userId = req.query.userId as string; // 获取用户 ID

  // 根据用户 ID 返回用户对剧集的权限
  if (id && episodes[id as string]) {
    const userEpisodes = episodes[id as string].map(episode => ({
      ...episode,
      isLocked: episode.isLocked && userId !== '1', // 假设用户1可以访问所有剧集
    }));
    res.status(200).json(userEpisodes);
  } else {
    // 未登录用户返回所有剧集信息，所有剧集的锁定状态为 true
    const allEpisodes = episodes[id as string].map(episode => ({
      ...episode,
      isLocked: true, // 所有剧集都锁定
    }));
    res.status(200).json(allEpisodes);
  }
}
